body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  align-items: center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  font-family: 'Overpass', sans-serif;
  height: 100vh;
  justify-content: center;
  text-align: center;
}

.app.hot {
  background-image: url(/weather-forecast/static/media/hot.977f9518.jpg);
}

.app.cold {
  background-image: url(/weather-forecast/static/media/cold.c23221d9.jpg);
}

.app.moderate {
  background-image: url(/weather-forecast/static/media/moderate.4360d9da.jpg);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

.forecast {
  margin: 0 auto;
  max-width: 800px;
}

.currently {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.currently span {
  display: block;
}

.seven-day-forecast {
  display: flex;
  flex-direction: row;
  margin: 16px;
  width: 600px;
}

.seven-day-forecast .day {
  align-items: center;
  display: flex;
  flex-direction: column;
  padding: 16px 0;
  width: 100%;
}

.seven-day-forecast .day:first-child {
  border-bottom-left-radius: 4px;
  border-top-left-radius: 4px;
}

.seven-day-forecast .day:last-child {
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
}

.condition {
  height: 64px;
  width: 64px;
}

.weather-card,
.day {
  background-color: rgba(255, 255, 255, 0.7);
}
.weather-card {
  align-items: center;
  border-radius: 4px;
  display: flex;
  justify-content: space-between;
  margin: 16px;
  width: 600px;
}

.location,
.weather {
  padding: 16px;
}

.location:not(h1):not(h3),
.weather {
  font-size: 12px;
}

.weather {
  font-size: 12px;
  text-align: right;
}

.location {
  text-align: left;
}

.location h1 {
  font-size: 32px;
  font-weight: bold;
  line-height: 30px;
}

.current-temperature {
  display: flex;
}

.current-temperature h1 {
  font-size: 40px;
}
.weather-and-date {
  display: flex;
  flex-direction: column;
}
.weather-card-lite {
  display: flex;
  flex-direction: column;
  font-size: 14px;
}
